@import '../utils/main.scss';
h3,
p {
  margin: 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
ul {
  padding: 0;
}
li {
  list-style: none;
}

.body-shopping-container {
  width: 100%;
  padding: 20px;
}
.book-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.book-card {
  position: relative;
  width: 100%;
  height: 170px;
  padding: 14px;
  border: 1px solid #ccc;
  border-radius: 16px;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  display: flex;
  gap: 14px;
}
.book-card:not:first-child {
  flex-direction: column;
}

.book-img {
  width: 100px;
  height: 142px;
  border-radius: 8px;
}

.book-text-content {
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.book-delete-btn {
  position: absolute;
  top: 14px;
  right: 14px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $accent-text-color;
  padding: 6px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  width: 28px;
  height: 28px;
  flex-shrink: 0;
}

.book-delete-icon {
  width: 16px;
  height: 16px;
}

.book-title {
  margin-bottom: 4px;
  max-width: calc(100% - 40px);
}

.book-desc {
  display: -webkit-box; /* Задаємо властивість контейнера flexbox */
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  white-space: normal;
  word-wrap: break-word;
}
.book-category {
  margin-bottom: 8px;
}

.book-copyright {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
}
// .book-author {
// //   margin-top: auto;
// }
.buy-links {
  height: 16px;
  display: flex;
  gap: 8px;
  justify-content: center;
}
// .buy-li {
//   height: 16px;
// }

.buy-amazon {
  align-content: flex-end;
  display: flex;
  flex-wrap: wrap;
}

.amazon-icon {
  height: 11px;
}

.amazon-book-buy-icon {
  display: flex;
  align-self: flex-end;
  width: 32px;
  height: 11px;
  filter: grayscale(100%);
}

.book-buy-icon {
  height: 16px;
  width: 16px;
  display: block;
  filter: grayscale(100%);
}

